</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers,
    },
    data() {
        return {
            swiperOption: {
                slidesPerView: 4,
                spaceBetween: 20,
                loop: true,
                effect: 'slide',
                observeParents: true,
                observer: true,
                autoplay: {
                    delay: 1500,
                    disableOnInteraction: false
                },
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                },
            },
            title: '首页',
            product_num: '',
            news_num: '',
            port_num: '',
            product_list: [],
            zhao_list: [],
            news_list: [],
            metu_list: [{
                title: '公司信息',
                path: '/home/manage'
            }, {
                title: '产品中心',
                path: '/home/product_manage'
            }, {
                title: '新闻中心',
                path: '/home/news_manage'
            }, {
                title: '招聘信息',
                path: '/home/post_manage'
            }]
        }
    },
    mounted() {
        /*产品数量*/
        this.$axios.post('/tomlive/productInfo/selectProductInfoCount').then((res) => {
                this.product_num = res.data.info
            }),
            /*新闻数量*/
            this.$axios.post('/tomlive/pressCenter/selectPressCenterCount').then((res) => {
                this.news_num = res.data.info
            }),
            /*招聘数量*/
            this.$axios.post('/tomlive/talentRecruitment/selectAllTalentRecruitmentSum').then((res) => {
                this.port_num = res.data.info
            }),
            /*核心产品*/
            this.$axios.post('/tomlive/productInfo/selectAllProductInfo').then((res) => {
                this.product_list = res.data.info
            }),
            /*招聘岗位*/
            this.$axios.post('/tomlive/talentRecruitment/selectAllTalentRecruitmentCount').then((res) => {
                this.zhao_list = res.data.info
            }),
            /*新闻动态*/
            this.$axios.post('/tomlive/pressCenter/selectAllPressCenter').then((res) => {
                this.news_list = res.data.info.reverse()
            })
    },
    methods: {
        /*查询更多*/
        more() {
            this.$router.push('/home/post_manage')
        },
        a(val){
            window.sessionStorage.setItem('product_details',JSON.stringify(val))
                this.$router.push('/home/product_details');
        }
    },
    computed: {
        list() {
            var _this = this
            return this.product_list.filter(function(number) {
                return number.status == 1
            })
        },
        list2() {
            var _this = this
            return this.news_list.filter(function(number) {
                return number.status == 1
            })
        }
    }
}
</script>
